<template>
    <div class="outBox">
        <van-nav-bar
        title="我的收藏"
        left-text="返回"
        left-arrow
        fixed
        @click-left="$router.go(-1)"
        />
        
        <div class="emptyContent" v-if="productLists.length<=0">
            <van-empty description="您还没有收藏的宝贝哦！" />
            <div class="goShopping" @click="goShopping"><i class="fa fa-arrow-right" aria-hidden="true"></i>去逛逛</div>
        </div>
        
        <!-- 商品 -->
        <div class="productBox" v-else>
            <div class="ProductItem" v-for="(item,index) in productLists" :key="index">
                <div class="imgBox" @click="goDetail(item.pid)">
                    <img :src="item.smallImg" />
                </div>
                <p class="productName">{{item.name}}</p>
                <p class="enName">{{item.enname}}</p>
                <div class="clearfix pd">
                    <span class="fl price">￥{{item.price}}</span>
                    <span class="deleteBox fr" @click="delProduct(item.pid,index)">
                        <van-icon class="delete-icon" name="delete" />
                  </span>
                </div>
            </div>

            
            
        </div>
    </div>
</template>

<script>
    import '../../assets/less/myCollect.less'
    export default {
        data(){
            return {
                productLists:[],
            }
        },
        created(){
            //查询所有收藏的商品
            this.findAllProduct()
        },
        methods:{
            //查询所有收藏的商品
            findAllProduct(){

                let tokenString = localStorage.getItem("__Tk");
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "GET",
                    url: "/findAllLike",
                    params: {
                    appkey: this.appkey,
                    tokenString,
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                        if(result.data.code == 2000){
                            this.productLists = result.data.result
                        }
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    });
            },

            //删除收藏商品
            delProduct(pid,index){
                
                let tokenString = localStorage.getItem("__Tk");
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: "/notlike",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        pid
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                        if(result.data.code == 900){
                            this.productLists.splice(index,1)
                        }
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    });
            },

            //跳转详情页
            goDetail(pid){
                this.$router.push({name:'detail',query:{pid}})
            },

            //跳转menu页
            goShopping(){
                this.$router.push({name:'menu'})
            }
        },
        
        
    }
</script>

<style lang="less" scoped>

</style>